* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #1e1e1e;
  direction: rtl;
  height: 100vh;
}

.todo-card {
  transition: transform 260ms ease, box-shadow 260ms ease, padding 200ms ease;
}
.todo-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.45);
}

.action-button {
  transition: background-color 160ms ease, box-shadow 160ms ease,
    transform 120ms ease;
}
.action-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.35);
}

/* Scrollbar styling for todo list area */
.todo-list {
  scrollbar-width: thin;
  scrollbar-color: rgba(144, 202, 249, 0.4) transparent;
}
.todo-list::-webkit-scrollbar {
  width: 10px;
}
.todo-list::-webkit-scrollbar-thumb {
  background: linear-gradient(
    180deg,
    rgba(144, 202, 249, 0.6),
    rgba(244, 143, 177, 0.6)
  );
  border-radius: 10px;
}
.todo-list::-webkit-scrollbar-track {
  background: transparent;
}

/* === Fonts === */
@font-face {
  font-family: Alex;
  src: url("/public/fonts/Alexandria-Bold.ttf");
  font-weight: bolder;
}
@font-face {
  font-family: Alex;
  src: url("/public/fonts/Alexandria-SemiBold.ttf");
  font-weight: bold;
}
@font-face {
  font-family: Alex;
  src: url("/public/fonts/Alexandria-Regular.ttf");
  font-weight: normal;
}
@font-face {
  font-family: Alex;
  src: url("/public/fonts/Alexandria-Light.ttf");
  font-weight: lighter;
}

